import { isUndefined, pick, uniq, inRange, isEqual, keys } from 'lodash-es';

import {
    FETCH_START,
    FETCH_ERROR,
    FETCH_SUCCESS,
    UPDATE_LOCKS,
    UPDATE_USERS,
    UPDATE_UI_PREFERENCES,
    UI_PREFERENCES_LOADED,
    SET_ACTIVE_GROUP,
    ADD_SELECTED_TICKETS,
    REMOVE_SELECTED_TICKETS,
    CLEAR_SELECTED_TICKETS,
    SET_SEARCH_FILTER,
    SET_SELECTED_BULK_ACTION,
    SET_SELECTED_PAGE,
    SET_TICKETS_PER_PAGE,
    SET_FILTER_PREFERENCES,
    SET_PRIMARY_ORDER,
    SET_PRIMARY_ORDER_BY,
} from './actions';
import { TicketGroups } from './constants';

export const defaultFilters = {
    dateFilter: '',
    replyStatusFilter: '',
    statusFilter: [],
    typeFilter: [],
    priorityFilter: [],
    productFilter: [],
    departmentFilter: [],
    assigneeFilter: [],
    authorFilter: [],
};

export const ticketReducerDefaults = {
    ticketsData: [], // raw tickets data, loaded from server
    tickets: [], // filtered and sorted tickets, ready for display
    selectedTickets: [],
    users: {},
    assigneeUserIds: [],
    authorUserIds: [],
    locks: {},
    selectedGroup: TicketGroups.ACTIVE,
    isTrash: false,
    isTicketsListLoading: true,
    isTicketsDataLoaded: false,
    isUIPreferencesLoaded: false,
    activeTicketsCount: 0,
    closedTicketsCount: 0,
    trashTicketsCount: 0,
    searchFilter: '',
    selectedBulkAction: '',
    selectedPage: 0,
    ticketsPerPage: 10,
    filterPreferences: defaultFilters,
    activeFiltersCount: 0,
    primaryOrder: 'desc',
    primaryOrderBy: 'openedDate',
    activeGroupFilterPreferencesCache: defaultFilters,
};

/**
 * Used in reducer to avoid unnecessary re-renders, because [] !== []
 * @param {*} state
 * @param {*} prop
 */
const resetStateArrayIfNotEmpty = (state, prop) =>
    state[prop].length ? [] : state[prop];

/**
 * Picks all available users from tickets to be used for filtering tickets list
 * @param {*} userIdProp
 * @param {*} ticketsData
 */
const pickUsersFromTicketsDataByProp = (userIdProp, ticketsData) =>
    uniq(ticketsData.map((ticket) => ticket[userIdProp]).filter(Boolean));

/**
 * @param {object[]} ticketsData
 * @param {object} filterPreferences
 * @param {string} searchFilter
 * @param {string} primaryOrder
 * @param {string} primaryOrderBy
 */
const getFilteredAndSortedTickets = (
    ticketsData,
    filterPreferences,
    searchFilter,
    primaryOrder,
    primaryOrderBy
) => {
    return ticketsData
        .filter((ticket) => {
            const {
                dateFilter,
                statusFilter,
                typeFilter,
                priorityFilter,
                productFilter,
                departmentFilter,
                assigneeFilter,
                authorFilter,
                replyStatusFilter,
            } = filterPreferences;

            const statusCheck =
                statusFilter.length > 0
                    ? statusFilter.includes(ticket.status)
                    : true;
            const typeCheck =
                typeFilter.length > 0 ? typeFilter.includes(ticket.type) : true;
            const priorityCheck =
                priorityFilter.length > 0
                    ? priorityFilter.includes(ticket.priority)
                    : true;
            const productCheck =
                productFilter.length > 0
                    ? productFilter.includes(ticket.product)
                    : true;
            const departmentCheck =
                departmentFilter.length > 0
                    ? departmentFilter.includes(ticket.department)
                    : true;
            const assigneeCheck =
                assigneeFilter.length > 0
                    ? assigneeFilter.includes(ticket.assigneeId) ||
                      (assigneeFilter.includes('unassigned') &&
                          ticket.assigneeId === null)
                    : true;
            const authorCheck =
                authorFilter.length > 0
                    ? authorFilter.includes(ticket.authorId)
                    : true;

            const searchFilterCheck = searchFilter
                ? ticket.title.toLowerCase().includes(searchFilter) ||
                  String(ticket.ticketId).includes(searchFilter)
                : true;

            let replyStatusCheck = true;

            if (replyStatusFilter === 'awaitingAgentReply') {
                replyStatusCheck = ticket.isAwaitingAgentReply;
            } else if (replyStatusFilter === 'awaitingCustomerReply') {
                replyStatusCheck = !ticket.isAwaitingAgentReply;
            } else if (replyStatusFilter === 'noReplies') {
                replyStatusCheck = ticket.repliesCount === 0;
            }

            const now = new Date().getTime(); // TODO: GMT
            const dayInMs = 1000 * 60 * 60 * 24;

            let dateCheck = true;

            if (dateFilter === '1day') {
                dateCheck = inRange(ticket.opened, now - dayInMs, now);
            } else if (dateFilter === '3days') {
                dateCheck = inRange(ticket.opened, now - 3 * dayInMs, now);
            } else if (dateFilter === '1week') {
                dateCheck = inRange(ticket.opened, now - 7 * dayInMs, now);
            } else if (dateFilter === '1month') {
                dateCheck = inRange(ticket.opened, now - 30 * dayInMs, now);
            } else if (dateFilter === '3months') {
                dateCheck = inRange(ticket.opened, now - 3 * 30 * dayInMs, now);
            } else if (dateFilter === '>1day') {
                dateCheck = now - dayInMs > ticket.opened;
            } else if (dateFilter === '>3days') {
                dateCheck = now - 3 * dayInMs > ticket.opened;
            } else if (dateFilter === '>1week') {
                dateCheck = now - 7 * dayInMs > ticket.opened;
            } else if (dateFilter === '>1month') {
                dateCheck = now - 30 * dayInMs > ticket.opened;
            } else if (dateFilter === '>3months') {
                dateCheck = now - 3 * 30 * dayInMs > ticket.opened;
            }

            return (
                dateCheck &&
                statusCheck &&
                typeCheck &&
                priorityCheck &&
                productCheck &&
                departmentCheck &&
                assigneeCheck &&
                authorCheck &&
                replyStatusCheck &&
                searchFilterCheck
            );
        })
        .sort((a, b) => {
            if (!primaryOrderBy) {
                return 0;
            }

            const direction = primaryOrder === 'asc' ? 1 : -1;

            switch (primaryOrderBy) {
                case 'openedDate':
                    return direction * (a.opened - b.opened);

                case 'replyDate':
                    return direction * (a.latestReplyTime - b.latestReplyTime);

                case 'id':
                    return (
                        direction * (Number(a.ticketId) - Number(b.ticketId))
                    );

                case 'title':
                    return direction * a.title.localeCompare(b.title);

                case 'numberOfReplies':
                    return direction * (a.repliesCount - b.repliesCount);

                default:
                    break;
            }

            return 0;
        });
};

/**
 * @param {object} filters
 * @returns {number}
 */
const countActiveFilters = (filters) =>
    keys(filters).filter(
        (key) => filters[key] && !isEqual(filters[key], defaultFilters[key])
    ).length;

const ticketsReducer = (state, action) => {
    const { type, payload } = action;

    switch (type) {
        case FETCH_START:
            return {
                ...state,
                isTicketsListLoading: true,
            };

        case FETCH_ERROR:
            return {
                ...state,
                isTicketsListLoading: false,
            };

        case FETCH_SUCCESS: {
            const {
                tickets,
                activeCount,
                closedCount,
                trashCount,
                users,
            } = payload;

            return {
                ...state,
                ticketsData: tickets,
                tickets: getFilteredAndSortedTickets(
                    tickets,
                    state.filterPreferences,
                    state.searchFilter,
                    state.primaryOrder,
                    state.primaryOrderBy
                ),
                activeTicketsCount: Number(activeCount),
                closedTicketsCount: Number(closedCount),
                trashTicketsCount: Number(trashCount),
                users,
                assigneeUserIds: pickUsersFromTicketsDataByProp(
                    'assigneeId',
                    tickets
                ).filter((id) => users[id]),
                authorUserIds: pickUsersFromTicketsDataByProp(
                    'authorId',
                    tickets
                ).filter((id) => users[id]),
                locks: tickets
                    .filter(({ lockingUserId }) => lockingUserId)
                    .reduce((all, { lockingUserId, postId }) => {
                        return {
                            ...all,
                            [postId]: lockingUserId,
                        };
                    }, {}),
                selectedTickets: resetStateArrayIfNotEmpty(
                    state,
                    'selectedTickets'
                ),
                selectedBulkAction: '',
                isTicketsListLoading: false,
                isTicketsDataLoaded: true,
            };
        }

        case SET_ACTIVE_GROUP: {
            const newFilters =
                payload.group === TicketGroups.ACTIVE
                    ? state.activeGroupFilterPreferencesCache
                    : defaultFilters;

            return {
                ...state,
                selectedTickets: resetStateArrayIfNotEmpty(
                    state,
                    'selectedTickets'
                ),
                searchFilter: '',
                selectedBulkAction: '',
                selectedPage: 0,
                isTrash: payload.group === TicketGroups.TRASH,
                selectedGroup: payload.group,
                activeGroupFilterPreferencesCache:
                    state.selectedGroup === TicketGroups.ACTIVE
                        ? state.filterPreferences
                        : state.activeGroupFilterPreferencesCache,
                filterPreferences: newFilters,
                activeFiltersCount: countActiveFilters(newFilters),
                // no need to update tickets here, because the list will be updated after tickets fetch
            };
        }

        case ADD_SELECTED_TICKETS:
            return {
                ...state,
                selectedTickets: uniq([
                    ...state.selectedTickets,
                    ...payload.selected,
                ]).sort(),
            };

        case REMOVE_SELECTED_TICKETS:
            return {
                ...state,
                selectedTickets: state.selectedTickets.filter(
                    (id) => !payload.selected.includes(id)
                ),
            };

        case CLEAR_SELECTED_TICKETS:
            return {
                ...state,
                selectedTickets: resetStateArrayIfNotEmpty(
                    state,
                    'selectedTickets'
                ),
            };

        case UPDATE_LOCKS:
            return {
                ...state,
                locks: payload.locks,
            };

        case UPDATE_USERS:
            return {
                ...state,
                users: { ...state.users, ...payload.users }, // there's no need to delete cached users at the moment
            };

        case UPDATE_UI_PREFERENCES: {
            const { ticketsPerPage, primaryOrder, primaryOrderBy } = payload;

            const newFilters = {
                ...state.filterPreferences,
                ...pick(payload.preferences, keys(defaultFilters)),
            };

            return {
                ...state,
                ticketsPerPage: !isUndefined(ticketsPerPage)
                    ? ticketsPerPage
                    : state.ticketsPerPage,
                primaryOrder: !isUndefined(primaryOrder)
                    ? primaryOrder
                    : state.primaryOrder,
                primaryOrderBy: !isUndefined(primaryOrderBy)
                    ? primaryOrderBy
                    : state.primaryOrderBy,
                filterPreferences: newFilters,
                activeFiltersCount: countActiveFilters(newFilters),
                tickets: getFilteredAndSortedTickets(
                    state.ticketsData,
                    newFilters,
                    state.searchFilter,
                    state.primaryOrder,
                    state.primaryOrderBy
                ),
            };
        }

        case UI_PREFERENCES_LOADED:
            return {
                ...state,
                isUIPreferencesLoaded: true,
            };

        case SET_SEARCH_FILTER:
            return {
                ...state,
                selectedTickets: resetStateArrayIfNotEmpty(
                    state,
                    'selectedTickets'
                ),
                searchFilter: payload.search,
                tickets: getFilteredAndSortedTickets(
                    state.ticketsData,
                    state.filterPreferences,
                    payload.search,
                    state.primaryOrder,
                    state.primaryOrderBy
                ),
            };

        case SET_SELECTED_BULK_ACTION:
            return {
                ...state,
                selectedBulkAction: payload.action,
            };

        case SET_SELECTED_PAGE:
            return {
                ...state,
                selectedTickets: resetStateArrayIfNotEmpty(
                    state,
                    'selectedTickets'
                ),
                selectedPage: payload.page,
            };

        case SET_TICKETS_PER_PAGE:
            return {
                ...state,
                selectedTickets: resetStateArrayIfNotEmpty(
                    state,
                    'selectedTickets'
                ),
                ticketsPerPage: payload.ticketsPerPage,
            };

        case SET_FILTER_PREFERENCES:
            return {
                ...state,
                filterPreferences: payload.preferences,
                activeFiltersCount: countActiveFilters(payload.preferences),
                tickets: getFilteredAndSortedTickets(
                    state.ticketsData,
                    payload.preferences,
                    state.search,
                    state.primaryOrder,
                    state.primaryOrderBy
                ),
            };

        case SET_PRIMARY_ORDER:
            return {
                ...state,
                primaryOrder: payload.order,
                tickets: getFilteredAndSortedTickets(
                    state.ticketsData,
                    state.filterPreferences,
                    state.search,
                    payload.order,
                    state.primaryOrderBy
                ),
            };

        case SET_PRIMARY_ORDER_BY:
            return {
                ...state,
                primaryOrderBy: payload.orderBy,
                tickets: getFilteredAndSortedTickets(
                    state.ticketsData,
                    state.filterPreferences,
                    state.search,
                    state.order,
                    payload.orderBy
                ),
            };

        default:
            throw new Error('Unknown action in tickets reducer');
    }
};

export default ticketsReducer;
