import React, { useEffect, useRef, useMemo, useReducer } from 'react';
import { slice } from 'lodash-es';

import TicketsListPageView from './TicketsListPageView';
import { saveUserUIPreferences } from './api';
import { TicketsContext, DispatchContext } from './context';
import ticketsReducer, { ticketReducerDefaults } from './reducer';
import { loadTickets, loadUserUIPreferences, loadLocks } from './actions';
import { TicketGroups } from './constants';

const TicketsListPage = () => {
    const [state, dispatch] = useReducer(ticketsReducer, ticketReducerDefaults);

    const {
        ticketsData,
        tickets,
        activeTicketsCount,
        locks,
        selectedGroup,
        isTicketsDataLoaded,
        isUIPreferencesLoaded,
        selectedPage,
        ticketsPerPage,
        filterPreferences,
        primaryOrder,
        primaryOrderBy,
    } = state;

    // init & ui prefs load
    useEffect(() => {
        window.toastr.options.positionClass = 'toast-top-right';
        window.toastr.options.timeOut = 10000;
        window.toastr.options.showDuration = 200;

        loadUserUIPreferences(dispatch);
    }, []);

    // tickets fetch
    useEffect(() => {
        loadTickets(dispatch, selectedGroup);
    }, [selectedGroup]);

    // check locked tickets periodically
    useEffect(() => {
        const CHECK_LOCKS_PERIOD = 10000;
        const checkLocksIntervalHandle = setInterval(
            () => loadLocks(dispatch, ticketsData, locks),
            CHECK_LOCKS_PERIOD
        );

        return () => clearInterval(checkLocksIntervalHandle);
    }, [ticketsData, locks]);

    // UI prefs autosave
    useEffect(() => {
        const storedPrefs = {
            ...filterPreferences,
            ticketsPerPage,
            primaryOrder,
            primaryOrderBy,
        };

        if (
            selectedGroup === TicketGroups.ACTIVE &&
            isTicketsDataLoaded &&
            isUIPreferencesLoaded
        ) {
            // NOTE: unnecessary autosave after switching back from Closed/Trash
            saveUserUIPreferences(storedPrefs);

            console.log('autosaving prefs...', storedPrefs);
        }
    }, [filterPreferences, ticketsPerPage, primaryOrder, primaryOrderBy]);

    // update WP menu active tickets count
    const wpTicketsCountRef = useRef(null);

    useEffect(() => {
        if (!wpTicketsCountRef.current) {
            wpTicketsCountRef.current = window.jQuery(
                '.js-mkb-active-tickets-count-wrap'
            );
        }

        if (!isTicketsDataLoaded) {
            return;
        }

        wpTicketsCountRef.current.html(
            activeTicketsCount
                ? `<span class="awaiting-mod count-${activeTicketsCount}">
                <span class="mkb-open-tickets" aria-hidden="true">${activeTicketsCount}</span>
                <span class="open-tickets-text screen-reader-text">${activeTicketsCount} open tickets</span>
            </span>`
                : ''
        );
    }, [ticketsData]);

    const hiddenByFilters = ticketsData.length - tickets.length;

    // pagination
    let currentPage = selectedPage;

    if (tickets.length / ticketsPerPage <= selectedPage) {
        currentPage = 0;
    }

    const currentPageTickets = useMemo(
        () =>
            slice(
                tickets,
                currentPage * ticketsPerPage,
                (currentPage + 1) * ticketsPerPage
            ),
        [tickets, ticketsPerPage, currentPage]
    );

    return (
        <DispatchContext.Provider value={dispatch}>
            <TicketsContext.Provider value={state}>
                <TicketsListPageView
                    {...{
                        hiddenByFilters,
                        currentPage,
                        ticketsCount: tickets.length,
                        currentPageTickets,
                    }}
                />
            </TicketsContext.Provider>
        </DispatchContext.Provider>
    );
};

export default TicketsListPage;
