export const getErrorMessageFromResponse = ({ errors: { global = [] } }) => {
    return global.length
        ? global[0].message
        : 'Some error has happened, try to refresh the page';
};

export function decodeHTMLEntities(text) {
    if (!text || !typeof text === 'string') {
        return text;
    }

    const entities = [
        ['amp', '&'],
        ['apos', "'"],
        ['#x27', "'"],
        ['#x2F', '/'],
        ['#39', "'"],
        ['#47', '/'],
        ['lt', '<'],
        ['gt', '>'],
        ['nbsp', ' '],
        ['quot', '"'],
    ];

    for (var i = 0, max = entities.length; i < max; ++i) {
        text = text.replace(
            new RegExp('&' + entities[i][0] + ';', 'g'),
            entities[i][1]
        );
    }

    return text;
}
