// @ts-check

import { isEqual } from 'lodash-es';

// @ts-ignore
import { getErrorMessageFromResponse } from 'utils';
import { BulkActionTypes } from './constants';
import {
    fetchTicketsList,
    fetchTicketLocks,
    fetchUserUIPreferences,
    moveTicketsToTrash,
    restoreTicketsFromTrash,
    permanentlyDeleteTickets,
} from './api';
import { defaultFilters } from './reducer';

// main tickets fetch
export const FETCH_START = 'FETCH_START';
export const FETCH_ERROR = 'FETCH_ERROR';
export const FETCH_SUCCESS = 'FETCH_SUCCESS';
// locks
export const UPDATE_LOCKS = 'UPDATE_LOCKS';
// UI preferences
export const UPDATE_UI_PREFERENCES = 'UPDATE_UI_PREFERENCES';
export const UI_PREFERENCES_LOADED = 'UI_PREFERENCES_LOADED ';
// users
export const UPDATE_USERS = 'UPDATE_USERS';
// group
export const SET_ACTIVE_GROUP = 'SET_ACTIVE_GROUP';
// selected
export const ADD_SELECTED_TICKETS = 'ADD_SELECTED_TICKETS';
export const REMOVE_SELECTED_TICKETS = 'REMOVE_SELECTED_TICKETS';
export const CLEAR_SELECTED_TICKETS = 'CLEAR_SELECTED_TICKETS';
// search
export const SET_SEARCH_FILTER = 'SET_SEARCH_FILTER';
// bulk actions
export const SET_SELECTED_BULK_ACTION = 'SET_SELECTED_BULK_ACTION';
// pagination
export const SET_SELECTED_PAGE = 'SET_SELECTED_PAGE';
export const SET_TICKETS_PER_PAGE = 'SET_TICKETS_PER_PAGE';
// filters
export const SET_FILTER_PREFERENCES = 'SET_FILTER_PREFERENCES';
// ordering
export const SET_PRIMARY_ORDER = 'SET_PRIMARY_ORDER';
export const SET_PRIMARY_ORDER_BY = 'SET_PRIMARY_ORDER_BY';

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {string} group
 */
export const loadTickets = async (dispatch, group) => {
    dispatch({ type: FETCH_START });

    let response;

    try {
        response = await fetchTicketsList(group);

        if (response.status === 1) {
            // @ts-ignore
            window.toastr.error(getErrorMessageFromResponse(response));

            dispatch({
                type: FETCH_ERROR,
                payload: response,
            });

            return;
        }

        dispatch({
            type: FETCH_SUCCESS,
            payload: response,
        });

        console.log('Tickets list', response.tickets);
    } catch (error) {
        // @ts-ignore
        window.toastr.error('Request error');

        dispatch({ type: FETCH_ERROR, payload: error });
    }
};

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {object} locks
 */
export function updateLocks(dispatch, locks) {
    dispatch({
        type: UPDATE_LOCKS,
        payload: { locks },
    });
}

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {object} users
 */
export const updateUsers = (dispatch, users) => {
    dispatch({
        type: UPDATE_USERS,
        payload: { users },
    });
};

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {object[]} tickets
 * @param {object} currentLocks
 */
export const loadLocks = async (dispatch, tickets, currentLocks) => {
    let response;

    try {
        response = await fetchTicketLocks(tickets);
    } catch (e) {
        console.error(e);
    }

    if (response.status === 1) {
        return; // no error message required here
    }

    const { locked: newLocks, users: lockingUsers } = response;

    if (isEqual(newLocks, currentLocks)) {
        return;
    }

    updateUsers(lockingUsers);

    const locks = newLocks ? newLocks : {};

    updateLocks(locks);
};

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {object} preferences
 */
export const loadUIPreferences = (
    dispatch,
    { ticketsPerPage, primaryOrder, primaryOrderBy, ...preferences }
) => {
    dispatch({
        type: UPDATE_UI_PREFERENCES,
        payload: { ticketsPerPage, primaryOrder, primaryOrderBy, preferences },
    });

    console.log('UI prefs for current user', preferences);
};

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 */
export const uiPreferencesLoaded = (dispatch) =>
    dispatch({ type: UI_PREFERENCES_LOADED });

