/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {string} group
 * @param {string} selectedGroup
 */
export const setActiveTicketsGroup = (dispatch, group, selectedGroup) => {
    if (group === selectedGroup) {
        return;
    }

    dispatch({ type: SET_ACTIVE_GROUP, payload: { group } });
};

