/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 */
export const loadUserUIPreferences = async (dispatch) => {
    let response;

    try {
        response = await fetchUserUIPreferences();

        if (response.status === 1) {
            // @ts-ignore
            window.toastr.error(getErrorMessageFromResponse(response));

            return;
        }

        const { preferences } = response; // {} is necessary, response can be null

        if (preferences) {
            loadUIPreferences(dispatch, preferences);
        }
    } catch (error) {
        // @ts-ignore
        window.toastr.error(getErrorMessageFromResponse(error));
    } finally {
        uiPreferencesLoaded(dispatch);
    }
};

/**
 * @param {(action: { type: string, payload?: object }) => void} dispatch
 * @param {string} selectedBulkAction
 * @param {number[]} selectedTickets
 * @param {string} selectedGroup
 */
export const handleBulkAction = (
    dispatch,
    selectedBulkAction,
    selectedTickets,
    selectedGroup
) => {
    console.log(
        'performing bulk action ' + selectedBulkAction,
        selectedTickets
    );

    if (!selectedBulkAction) {
        alert('No bulk action is selected');

        return;
    }

    if (!selectedTickets.length) {
        alert('No tickets are selected');

        return;
    }

    const count = selectedTickets.length;

    const bulkActionsMap = {
        [BulkActionTypes.TRASH]: {
            handler: moveTicketsToTrash,
            confirm: `Are you sure you want to move (${count}) tickets to the trash?`,
            successMessage: `${count} tickets were successfully moved to trash`,
        },
        [BulkActionTypes.RESTORE]: {
            handler: restoreTicketsFromTrash,
            confirm: `Are you sure you want to restore (${count}) tickets?`,
            successMessage: `${count} tickets were successfully restored`,
        },
        [BulkActionTypes.DELETE]: {
            handler: permanentlyDeleteTickets,
            confirm: `Are you sure you want to permanently delete (${count}) tickets?`,
            successMessage: `${count} tickets were deleted permanently`,
        },
    };

    const bulkAction = bulkActionsMap[selectedBulkAction];

    if (!confirm(bulkAction.confirm)) {
        return;
    }

    dispatch({ type: FETCH_START });

    if (!bulkAction) {
        return;
    }

    let response;

    try {
        response = bulkAction.handler(selectedTickets);

        if (response.status === 1) {
            // @ts-ignore
            window.toastr.error(getErrorMessageFromResponse(response));

            return;
        }

        // @ts-ignore
        window.toastr.success(bulkAction.successMessage);

        // wp counts are not updated immediately
        setTimeout(() => {
            loadTickets(dispatch, selectedGroup);
        }, 1000);
    } catch (error) {
        dispatch({ type: FETCH_SUCCESS }); // ignore the errors for now
    }
};
