import React from 'react';

import { decodeHTMLEntities } from 'utils';

const InlineActions = ({
    isTrash,
    isLocked,
    title,
    link,
    editLink,
    deleteLink,
    forceDeleteLink,
    restoreLink,
}) => {
    let actions = isTrash
        ? [
              {
                  className: 'untrash',
                  actionLink: restoreLink,
                  ariaLabel: `Restore ${title} from the Trash`,
                  label: 'Restore',
              },
              {
                  className: 'delete',
                  linkClassName: 'submitdelete',
                  actionLink: forceDeleteLink,
                  ariaLabel: `Delete ${title} permanently`,
                  label: 'Delete Permanently',
              },
          ]
        : [
              {
                  className: 'edit',
                  actionLink: editLink,
                  ariaLabel: `Edit ${title}`,
                  label: 'Edit',
              },
              {
                  className: 'trash',
                  linkClassName: 'submitdelete',
                  actionLink: deleteLink,
                  ariaLabel: `Move ${title} to the Trash`,
                  label: 'Trash',
              },
              {
                  className: 'view',
                  actionLink: link,
                  rel: 'bookmark noreferrer',
                  target: '_blank',
                  ariaLabel: `View ${title}`,
                  label: 'View',
              },
          ];

    actions = actions.filter(
        ({ actionLink, className }) =>
            Boolean(actionLink) && (!isLocked || className !== 'trash')
    );

    return actions.map(
        (
            {
                className,
                linkClassName,
                actionLink,
                rel,
                target,
                ariaLabel,
                label,
            },
            index
        ) => (
            <span
                key={'inline-action-' + className.toLowerCase()}
                className={className}
            >
                <a
                    href={decodeHTMLEntities(actionLink)}
                    className={linkClassName}
                    aria-label={ariaLabel}
                    rel={rel}
                    target={target}
                >
                    {label}
                </a>
                {index < actions.length - 1 ? ` | ` : null}
            </span>
        )
    );
};

export default React.memo(InlineActions);
