import React, { useContext } from 'react';
import { Tag, Select } from 'antd';

import { TicketsContext, DispatchContext } from './context';
import { setFilterPreferences, resetFilters } from './actions';

import style from './index.css';

const taxByIdReducer = (all, item) => ({ ...all, [item.term_id]: item });
const statusByIdReducer = (all, item) => ({ ...all, [item.id]: item });

const ticketStatuses = window.MinervaTicketsList.statuses || [];
const ticketStatusesById = ticketStatuses.reduce(statusByIdReducer, {});

const ticketTypes = window.MinervaTicketsList.types || [];
const ticketTypesById = ticketTypes.reduce(taxByIdReducer, {});

const ticketPriorities = window.MinervaTicketsList.priorities || [];
const ticketPrioritiesById = ticketPriorities.reduce(taxByIdReducer, {});

const ticketProducts = window.MinervaTicketsList.products || [];

const ticketDepartments = window.MinervaTicketsList.departments || [];

// components
const StatusTag = ({ label, value, closable, onClose }) => (
    <Tag
        color={ticketStatusesById[value].color}
        closable={closable}
        onClose={onClose}
        style={{ marginRight: 3 }}
    >
        {label}
    </Tag>
);

const TypeTag = ({ label, value, closable, onClose }) => (
    <Tag
        color={ticketTypesById[value].color}
        closable={closable}
        onClose={onClose}
        style={{ marginRight: 3 }}
    >
        {label}
    </Tag>
);

const PriorityTag = ({ label, value, closable, onClose }) => (
    <Tag
        color={ticketPrioritiesById[value].color}
        closable={closable}
        onClose={onClose}
        style={{ marginRight: 3 }}
    >
        {label}
    </Tag>
);

// filters
const Filters = () => {
    const {
        selectedGroup: group,
        users,
        filterPreferences,
        assigneeUserIds,
        authorUserIds,
    } = useContext(TicketsContext);
    const dispatch = useContext(DispatchContext);

    return (
        <div>
            <div style={{ marginBottom: '2em' }}>
                <div className={style.filterCell}>
                    <label className={style.filterLabel}>Select Date:</label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                dateFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        allowClear={true}
                        placeholder="Select a period"
                        options={[
                            { value: '1day', label: '24 hours' },
                            { value: '3days', label: '3 days' },
                            { value: '1week', label: '1 week' },
                            { value: '1month', label: '1 month' },
                            { value: '3months', label: '3 months' },
                            { value: '>1day', label: 'Older than 24 hours' },
                            { value: '>3days', label: 'Older than 3 days' },
                            { value: '>1week', label: 'Older than 1 week' },
                            { value: '>1month', label: 'Older than 1 month' },
                            { value: '>3months', label: 'Older than 3 months' },
                            // {value: 'custom', label: 'Custom'},
                        ]}
                        value={filterPreferences.dateFilter || null}
                    />
                </div>

                {group !== 'closed' && (
                    <div className={style.filterCell}>
                        <label className={style.filterLabel}>
                            Select Status:
                        </label>
                        <Select
                            onChange={(value) =>
                                setFilterPreferences(dispatch, {
                                    ...filterPreferences,
                                    statusFilter: value,
                                })
                            }
                            style={{ width: '100%' }}
                            mode="multiple"
                            allowClear={true}
                            placeholder="Select statuses"
                            tagRender={StatusTag}
                            options={ticketStatuses
                                .filter(({ id }) =>
                                    group === 'active' ? id !== 'closed' : true
                                )
                                .map(({ id, label }) => ({ value: id, label }))}
                            value={filterPreferences.statusFilter}
                        />
                    </div>
                )}

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>
                        Select Reply Status:
                    </label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                replyStatusFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        allowClear={true}
                        placeholder="Select reply status"
                        options={[
                            {
                                value: 'awaitingAgentReply',
                                label: 'Awaiting Agent Reply',
                            },
                            {
                                value: 'awaitingCustomerReply',
                                label: 'Awaiting Customer Reply',
                            },
                            { value: 'noReplies', label: 'No Replies' },
                        ]}
                        value={filterPreferences.replyStatusFilter || null}
                    />
                </div>

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>Select Type:</label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                typeFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        mode="multiple"
                        allowClear={true}
                        placeholder="Select types"
                        tagRender={TypeTag}
                        options={ticketTypes.map(({ term_id, name }) => ({
                            value: term_id,
                            label: name,
                        }))}
                        value={filterPreferences.typeFilter}
                    />
                </div>

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>
                        Select Priority:
                    </label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                priorityFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        mode="multiple"
                        allowClear={true}
                        tagRender={PriorityTag}
                        placeholder="Select priorities"
                        options={ticketPriorities.map(({ term_id, name }) => ({
                            value: term_id,
                            label: name,
                        }))}
                        value={filterPreferences.priorityFilter}
                    />
                </div>

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>
                        Select Department:
                    </label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                departmentFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        mode="multiple"
                        allowClear={true}
                        placeholder="Select departments"
                        options={ticketDepartments.map(({ term_id, name }) => ({
                            value: term_id,
                            label: name,
                        }))}
                        value={filterPreferences.departmentFilter}
                    />
                </div>

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>Select Product:</label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                productFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        mode="multiple"
                        allowClear={true}
                        placeholder="Select products"
                        options={ticketProducts.map(({ term_id, name }) => ({
                            value: term_id,
                            label: name,
                        }))}
                        value={filterPreferences.productFilter}
                    />
                </div>

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>
                        Select Assignee:
                    </label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                assigneeFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        mode="multiple"
                        allowClear={true}
                        placeholder="Select agents"
                        options={[
                            { value: 'unassigned', label: 'Unassigned' },
                            ...assigneeUserIds.map((id) => ({
                                value: id,
                                label: users[id].displayName,
                            })),
                        ]}
                        value={filterPreferences.assigneeFilter}
                    />
                </div>

                <div className={style.filterCell}>
                    <label className={style.filterLabel}>
                        Select Customer:
                    </label>
                    <Select
                        onChange={(value) =>
                            setFilterPreferences(dispatch, {
                                ...filterPreferences,
                                authorFilter: value,
                            })
                        }
                        style={{ width: '100%' }}
                        mode="multiple"
                        allowClear={true}
                        placeholder="Select users"
                        options={[
                            ...authorUserIds.map((id) => ({
                                value: id,
                                label: users[id].displayName,
                            })),
                        ]}
                        value={filterPreferences.authorFilter}
                    />
                </div>
            </div>

            <div style={{ marginBottom: '2em' }}>
                <div
                    className="button action"
                    onClick={() => resetFilters(dispatch)}
                >
                    <i className="fa fa-undo"></i> Clear Filters
                </div>
            </div>
        </div>
    );
};

export default React.memo(Filters);
