import React, { useContext } from 'react';

import { TicketsContext, DispatchContext } from './context';
import { setSelectedBulkAction, handleBulkAction } from './actions';
import { BulkActionTypes } from './constants';

const BulkActions = () => {
    const {
        tickets,
        selectedBulkAction,
        selectedTickets,
        selectedGroup,
        isTicketsListLoading,
        isTrash,
    } = useContext(TicketsContext);
    const dispatch = useContext(DispatchContext);

    const isDisabled = tickets.length === 0 || isTicketsListLoading;

    const actions = isTrash
        ? [
              {
                  value: BulkActionTypes.RESTORE,
                  label: 'Restore',
              },
              {
                  value: BulkActionTypes.DELETE,
                  label: 'Delete Permanently',
              },
          ]
        : [{ value: BulkActionTypes.TRASH, label: 'Move to Trash' }];
    return (
        <>
            <label
                htmlFor="bulk-action-selector-top"
                className="screen-reader-text"
            >
                Select bulk action
            </label>
            <select
                name="action"
                id="bulk-action-selector-top"
                value={selectedBulkAction}
                onChange={(e) =>
                    setSelectedBulkAction(dispatch, e.target.value)
                }
                disabled={isDisabled}
            >
                <option value="">Bulk actions</option>
                {actions.map(({ value, label }) => (
                    <option key={value} value={value}>
                        {label}
                    </option>
                ))}
                {/* TODO: other actions */}
            </select>
            <input
                type="submit"
                className="button action"
                value="Apply"
                disabled={isDisabled}
                onClick={(e) => {
                    e.preventDefault();

                    handleBulkAction(
                        dispatch,
                        selectedBulkAction,
                        selectedTickets,
                        selectedGroup
                    );
                }}
            />{' '}
        </>
    );
};

export default React.memo(BulkActions);
