import React from 'react';

import style from './index.css';

// components
const FreshnessBadge = ({ latestReplyTime }) => {
    const now = new Date().getTime();
    const elapsedTime = now - latestReplyTime; // in GMT
    const elapsedHours = elapsedTime / (60 * 60 * 1000);

    let color = '#2cbe4e';

    if (elapsedHours > 24) {
        color = '#FF8C23';
    }

    if (elapsedHours > 48) {
        color = '#EB2F2D';
    }

    return (
        <span
            className={style.freshnessBadge}
            style={{ backgroundColor: color }}
        >
            <span className={style.freshnessLegend}>
                Tickets awaiting agent reply:
                <ul>
                    <li>
                        <span
                            className={style.freshnessLegendSample}
                            style={{ backgroundColor: '#2cbe4e' }}
                        ></span>{' '}
                        up to 1 day since latest customer reply
                    </li>
                    <li>
                        <span
                            className={style.freshnessLegendSample}
                            style={{ backgroundColor: '#FF8C23' }}
                        ></span>{' '}
                        more than 1 day since latest customer reply
                    </li>
                    <li>
                        <span
                            className={style.freshnessLegendSample}
                            style={{ backgroundColor: '#EB2F2D' }}
                        ></span>{' '}
                        more than 2 days since latest customer reply
                    </li>
                </ul>
            </span>
        </span>
    );
};

export default React.memo(FreshnessBadge);
