const ui = window.MinervaUI;

export const fetchTicketsList = (group) => {
    return ui.fetch(
        {
            action: 'mkb_get_tickets_list',
            group,
        },
        { method: 'GET' }
    );
};

export const fetchTicketLocks = (tickets) => {
    return ui.fetch(
        {
            action: 'mkb_get_tickets_lock_info',
            ids: tickets.map(({ postId }) => postId),
        },
        { method: 'GET' }
    );
};

export const fetchUserUIPreferences = () => {
    return ui.fetch(
        {
            action: 'mkb_get_tickets_list_ui_preferences',
        },
        { method: 'GET' }
    );
};

export const saveUserUIPreferences = (preferences) => {
    return ui.fetch({
        action: 'mkb_save_tickets_list_ui_preferences',
        preferences,
    });
};

export const moveTicketsToTrash = (ticketIds) => {
    return ui.fetch({
        action: 'mkb_tickets_bulk_trash',
        ticketIds,
    });
};

export const restoreTicketsFromTrash = (ticketIds) => {
    return ui.fetch({
        action: 'mkb_tickets_bulk_restore',
        ticketIds,
    });
};

export const permanentlyDeleteTickets = (ticketIds) => {
    return ui.fetch({
        action: 'mkb_tickets_bulk_permanently_delete',
        ticketIds,
    });
};
