import React, { useContext } from 'react';
import { Pagination as AntPagination } from 'antd';

import { TicketsContext, DispatchContext } from './context';
import { setTicketsPerPage, setSelectedPage } from './actions';

const TicketsListSearch = ({ currentPage, ticketsCount }) => {
    const { tickets, isTicketsListLoading, ticketsPerPage } = useContext(
        TicketsContext
    );
    const dispatch = useContext(DispatchContext);
    const isDisabled = tickets.length === 0 || isTicketsListLoading;

    return (
        <AntPagination
            showSizeChanger
            pageSizeOptions={[5, 10, 20, 50, 100]}
            showTotal={(total, range) =>
                `${range[0]}-${range[1]} of ${total} tickets`
            }
            defaultPageSize={ticketsPerPage}
            pageSize={ticketsPerPage}
            current={currentPage + 1}
            total={ticketsCount}
            disabled={isDisabled}
            onShowSizeChange={(current, size) =>
                setTicketsPerPage(dispatch, size)
            }
            onChange={(page) => {
                setSelectedPage(dispatch, page - 1);
            }}
        />
    );
};

export default React.memo(TicketsListSearch);
