import React, { useState, useContext } from 'react';

import Badge from 'components/Badge';
import ErrorBoundary from 'components/ErrorBoundary';
import FreshnessBadge from './FreshnessBadge';
import InlineActions from './InlineActions';
import { TicketsContext, DispatchContext } from './context';
import {
    addTicketsToSelected,
    removeTicketsFromSelected,
    clearSelectedTickets,
} from './actions';
import { decodeHTMLEntities } from 'utils';
import style from './index.css';

const taxByIdReducer = (all, item) => ({ ...all, [item.term_id]: item });
const statusByIdReducer = (all, item) => ({ ...all, [item.id]: item });

const ticketStatuses = window.MinervaTicketsList.statuses || [];
const ticketStatusesById = ticketStatuses.reduce(statusByIdReducer, {});

const ticketTypes = window.MinervaTicketsList.types || [];
const ticketTypesById = ticketTypes.reduce(taxByIdReducer, {});

const ticketPriorities = window.MinervaTicketsList.priorities || [];
const ticketPrioritiesById = ticketPriorities.reduce(taxByIdReducer, {});

const ticketProducts = window.MinervaTicketsList.products || [];
const ticketProductsById = ticketProducts.reduce(taxByIdReducer, {});

const TicketRow = ({
    postId,
    ticketId,
    isGuest,
    opened,
    openerFirstName,
    openerLastName,
    isAwaitingAgentReply,
    assigneeId,
    authorId,

    ...ticket
}) => {
    const { users, locks, selectedTickets, isTrash } = useContext(
        TicketsContext
    );
    const dispatch = useContext(DispatchContext);

    const status = ticketStatusesById[ticket.status] || null;
    const type = ticketTypesById[ticket.type] || null;
    const priority = ticketPrioritiesById[ticket.priority] || null;
    const product = ticketProductsById[ticket.product] || null;

    let guestLabel = '';

    if (isGuest) {
        guestLabel =
            openerFirstName || openerLastName
                ? [openerFirstName, openerLastName].filter(Boolean).join(' ') +
                  ' ' +
                  '(Guest)'
                : 'Guest';
    }

    let assigneeUser = null;

    if (assigneeId) {
        assigneeUser = users[assigneeId];
    }

    let authorUser = null;

    if (authorId) {
        authorUser = users[authorId];
    }

    const openedDate = new Date(opened);

    const isLocked = Boolean(locks[postId]);
    const lockingUser = isLocked && users[locks[postId]];

    return (
        <tr className={isLocked ? 'wp-locked' : undefined}>
            {/* Ticket Row Bulk Actions Checkbox */}
            <th scope="row" className={style.checkColumn}>
                <label
                    className="screen-reader-text"
                    htmlFor={`ticket-select-${postId}`}
                >
                    Select {ticket.title}
                </label>
                <input
                    id={`ticket-select-${postId}`}
                    type="checkbox"
                    onChange={(e) => {
                        const isChecked = e.target.checked;

                        if (isChecked) {
                            addTicketsToSelected(dispatch, [postId]);
                        } else {
                            removeTicketsFromSelected(dispatch, [postId]);
                        }
                    }}
                    checked={selectedTickets.includes(postId)}
                />
                {isLocked && (
                    <div className="locked-indicator">
                        <span
                            className="locked-indicator-icon"
                            aria-hidden="true"
                        ></span>
                        <span className="screen-reader-text">
                            {ticket.title} is locked
                        </span>
                    </div>
                )}
            </th>

            {/* Ticket Status */}
            <td>
                {status && (
                    <>
                        {isAwaitingAgentReply && (
                            <FreshnessBadge
                                latestReplyTime={ticket.latestReplyTime}
                            />
                        )}
                        <Badge color={status.color}>
                            <i className={`fa ${status.icon}`}></i>{' '}
                            {status.label}
                        </Badge>
                    </>
                )}
            </td>

            {/* Ticket Title */}
            <td className="title column-title has-row-actions column-primary page-title">
                {isLocked && (
                    <div className="locked-info">
                        <span
                            className="locked-avatar"
                            dangerouslySetInnerHTML={{
                                __html: lockingUser.avatarHTML,
                            }}
                        >
                            {}
                        </span>
                        &nbsp;
                        <span className="locked-text">
                            {lockingUser.displayName} is currently editing
                        </span>
                    </div>
                )}

                <strong>
                    {isTrash ? (
                        <span>{ticket.title}</span>
                    ) : (
                        <a
                            className="row-title"
                            href={decodeHTMLEntities(ticket.editLink)}
                            aria-label={`${ticket.title} (Edit)`}
                        >
                            {ticket.title}
                        </a>
                    )}
                </strong>

                {/* Inline Edit Actions */}
                <div className="row-actions">
                    <ErrorBoundary>
                        <InlineActions
                            isTrash={isTrash}
                            isLocked={isLocked}
                            title={ticket.title}
                            link={ticket.link}
                            editLink={ticket.editLink}
                            deleteLink={ticket.deleteLink}
                            forceDeleteLink={ticket.forceDeleteLink}
                            restoreLink={ticket.restoreLink}
                        />
                    </ErrorBoundary>
                </div>
            </td>

            {/* Ticket Priority */}
            <td>
                {priority && (
                    <Badge color={priority.color}>{priority.name}</Badge>
                )}
            </td>

            {/* Ticket Assignee */}
            <td>
                {assigneeId ? (
                    assigneeUser ? (
                        <a href={assigneeUser.profileLink}>
                            {assigneeUser.displayName}
                        </a>
                    ) : (
                        '[DELETED]'
                    )
                ) : (
                    'Unassigned'
                )}
            </td>

            {/* Ticket Product */}
            <td>{product && product.name}</td>

            {/* Ticket ID */}
            <td>{ticketId}</td>

            {/* Ticket Type */}
            <td>{type && <Badge color={type.color}>{type.name}</Badge>}</td>

            {/* Ticket Open Date */}
            <td>{openedDate.toLocaleString()}</td>

            {/* Ticket Opened By */}
            <td>
                {isGuest ? (
                    guestLabel
                ) : authorId ? (
                    authorUser ? (
                        <a href={authorUser.profileLink}>
                            {authorUser.displayName}
                        </a>
                    ) : (
                        '[DELETED]'
                    )
                ) : (
                    'Not set'
                )}
            </td>

            {/* Ticket Activity */}
            <td>
                <strong>{ticket.repliesCount}</strong> replies
                {isAwaitingAgentReply && ', awaiting agent reply'}
            </td>
        </tr>
    );
};

const TableHead = ({ type, isCheckAll, handleCheckAll }) => {
    const Root = type === 'header' ? 'thead' : 'tfoot';
    const checkAllId = `tickets-${type}-check-all`;

    return (
        <Root>
            <tr>
                <th className={style.checkColumn}>
                    <label className="screen-reader-text" htmlFor={checkAllId}>
                        Select All
                    </label>
                    <input
                        id={checkAllId}
                        type="checkbox"
                        checked={isCheckAll}
                        onChange={(e) => handleCheckAll(e.target.checked)}
                    />
                </th>
                <th scope="col">Status</th>
                <th scope="col">Title</th>
                <th scope="col">Priority</th>
                <th scope="col">Assigned to</th>
                <th scope="col">Product</th>
                <th scope="col">Ticket ID</th>
                <th scope="col">Type</th>
                <th scope="col">Opened</th>
                <th scope="col">Opened by</th>
                <th scope="col">Activity</th>
            </tr>
        </Root>
    );
};

// table
const TicketsListTable = ({ tickets }) => {
    const dispatch = useContext(DispatchContext);
    const [isCheckAll, setIsCheckAll] = useState(false);

    const handleCheckAll = (isChecked) => {
        setIsCheckAll(isChecked);

        isChecked
            ? addTicketsToSelected(
                  dispatch,
                  tickets.map(({ postId }) => postId)
              )
            : clearSelectedTickets(dispatch);
    };

    return (
        <table className="widefat fixed striped">
            <TableHead
                type="header"
                isCheckAll={isCheckAll}
                handleCheckAll={handleCheckAll}
            />

            <tbody>
                {tickets.map((ticket) => (
                    <ErrorBoundary key={`ticket-list-row-${ticket.postId}`}>
                        <TicketRow {...ticket} />
                    </ErrorBoundary>
                ))}
            </tbody>

            <TableHead
                type="footer"
                isCheckAll={isCheckAll}
                handleCheckAll={handleCheckAll}
            />
        </table>
    );
};

export default React.memo(TicketsListTable);
