import React, { useContext } from 'react';

import { TicketsContext, DispatchContext } from './context';
import { setSearchFilter } from './actions';

const Search = () => {
    const { tickets, isTicketsListLoading, searchFilter } = useContext(
        TicketsContext
    );
    const dispatch = useContext(DispatchContext);
    const isDisabled = tickets.length === 0 || isTicketsListLoading;

    return (
        <>
            <label className="screen-reader-text" htmlFor="post-search-input">
                Search Tickets:
            </label>
            <input
                type="search"
                placeholder="Search by title or ID"
                disabled={isDisabled}
                value={searchFilter}
                onInput={(e) =>
                    setSearchFilter(dispatch, e.currentTarget.value.trim())
                }
            />
        </>
    );
};

export default React.memo(Search);
