import React, { useContext } from 'react';

import { TicketGroups } from './constants';
import { TicketsContext, DispatchContext } from './context';
import { setActiveTicketsGroup } from './actions';

const TicketGroupsMenu = () => {
    const {
        selectedGroup,
        activeTicketsCount,
        closedTicketsCount,
        trashTicketsCount,
    } = useContext(TicketsContext);
    const dispatch = useContext(DispatchContext);

    const ticketGroups = [
        {
            className: 'all',
            label: 'Active',
            group: TicketGroups.ACTIVE,
            count: activeTicketsCount,
        },
        {
            className: 'closed',
            label: 'Closed',
            group: TicketGroups.CLOSED,
            count: closedTicketsCount,
        },
        {
            className: 'trash',
            label: 'Trash',
            group: TicketGroups.TRASH,
            count: trashTicketsCount,
        },
    ];

    return (
        <ul className="subsubsub">
            {ticketGroups.map(({ group, label, className, count }, index) => (
                <li key={group} className={className}>
                    <a
                        href="#"
                        className={selectedGroup === group ? 'current' : ''}
                        onClick={(e) => {
                            e.preventDefault();

                            setActiveTicketsGroup(
                                dispatch,
                                group,
                                selectedGroup
                            );
                        }}
                    >
                        {label} <span className="count">({count})</span>
                    </a>{' '}
                    {index < ticketGroups.length - 1 ? '|' : null}
                </li>
            ))}
        </ul>
    );
};

export default React.memo(TicketGroupsMenu);
