import React, { useContext } from 'react';
import { Select } from 'antd';

import { TicketsContext, DispatchContext } from './context';
import { setPrimaryOrderBy, setPrimaryOrder } from './actions';

import style from './index.css';

const Sorting = () => {
    const { primaryOrderBy, primaryOrder } = useContext(TicketsContext);
    const dispatch = useContext(DispatchContext);

    return (
        <div>
            <div className={style.filterCell}>
                <label className={style.filterLabel}>Order By:</label>
                <Select
                    onChange={(value) => setPrimaryOrderBy(dispatch, value)}
                    style={{ width: '100%' }}
                    placeholder="Select order field"
                    options={[
                        { value: 'openedDate', label: 'Opened date' },
                        { value: 'replyDate', label: 'Latest reply date' },
                        { value: 'id', label: 'ID' },
                        { value: 'title', label: 'Title' },
                        {
                            value: 'numberOfReplies',
                            label: 'Number of replies',
                        },
                    ]}
                    value={primaryOrderBy}
                />
            </div>
            <div className={style.filterCell}>
                <label className={style.filterLabel}>Order:</label>
                <Select
                    onChange={(value) => setPrimaryOrder(dispatch, value)}
                    style={{ width: '100%' }}
                    placeholder="Select order"
                    options={[
                        { value: 'asc', label: 'Ascending' },
                        { value: 'desc', label: 'Descending' },
                    ]}
                    value={primaryOrder}
                />
            </div>
        </div>
    );
};

export default React.memo(Sorting);
